import React from "react";

import "./Responsive.css"

export default function Responsive({isNavOpen}) {
  return (
    <ul className={`nav-responsive ${isNavOpen && "nav-responsive-open"}`}>
      <li>Home</li>
      <li>Projects</li>
      <li>Skills</li>
      <li>About</li>
      <li>Contact</li>
    </ul>
  );
}
